.new-todo {
  display: flex;
  gap: 20px;
}

.filter {
  margin-bottom: 20px;
}

.todo-list {
  border-collapse: collapse;
}

.todo-list > thead {
  background-color: #f0f0f0;
}

.todo-list > thead > tr > th,
.todo-list > tbody > tr > td {
  width: 150px;
  border: 1px solid #333;
  padding: 10px;
}
